/* eslint-disable */
import axios from 'axios'
import { API, ENDPOINT, USERLEVELS} from './constant'
import { withCookies, Cookies } from 'react-cookie';


const cookies = new Cookies();
const token = localStorage.getItem('loggedin_token');

axios.interceptors.response.use((response) => response, (error) => {
    if(error?.response?.status === 401 && !cookies.token && localStorage.getItem('userData') !== null ){
        //window.location = '/login?msg=session_expired'
        localStorage.removeItem("loggedin_id")
        localStorage.removeItem("loggedin_token")
        localStorage.removeItem("userData")
        const userData = JSON.parse(localStorage.getItem('userData'));
        window.location= userData.userlevel !== USERLEVELS.ADMIN ? '/login?msg=session_expired' : '/admin-login?msg=session_expired'
    }
    throw error;
  });

