/* eslint-disable */
import React, { useState, useEffect, useMemo, useRef } from "react";
import { Link, useHistory } from 'react-router-dom';
import DataTable from "react-data-table-component";
import ApiServices from "@/utils/ApiServices";
import Empty from "@/components/Empty";
import { TABLE_SETTINGS } from "@/utils/constant";
import { Button, Row, Col } from 'reactstrap';
import CustomPagination from '@/components/CustomPagination'
import ToggleSwitch from '@/components/ToggleSwitch'

const PropertyBilling = ({id, isTotal}) => {     
    const history = useHistory();
    const [data, setData] = useState([]);
    const [loading, setLoading] = useState(true);
    const [totalRows, setTotalRows] = useState(0);
    const [perPage, setPerPage] = useState(TABLE_SETTINGS.PAGINATION_PER_PAGE);
    const [currentPage, setCurrentPage] = useState(1);
    const [rowCount, setRowCount] = useState(0)
    const page_ref = useRef(1);
    const saerch_ref = useRef(null);

    const columns = useMemo(
        () => [
            {
                name: "Tax Year",
                selector: "year",
                sortable: false,
            },
            {
                name: "Noticed Value",
                selector: "noticedValue",
                sortable: false,
            },
            {
                name: "Settled Value",
                selector: "settledValue",
                sortable: false,
            },
            {
                name: "Amount of Reduction",
                selector: "reduction",
                sortable: false,
            },
            {
                name: "Tax Rate",
                selector: "taxrate",
                sortable: false,
            },
            {
                name: "Tax Savings",
                selector: "taxsavings",
                sortable: false,
            },
            {
                name: "Billing Percentage",
                selector: "billingPercentage",
                sortable: false,
            },
            {
                name: "Invoice Amount",
                selector: "invoiceamount",
                sortable: false,
            },
            {
                name: "Results",
                selector: "result",
                sortable: false,
            },
            {
                name: "Agent",
                selector: "agent_id",
                sortable: false,
            },
            {
                name: 'Invoice paid',
                cell: (row) => (
                    <>
                    <ToggleSwitch
                        id={`general-switch-${row.id}`}
                        checked={ row.invoiceamountOrig === 0 ? true : false}
                        disabled={false}
                    />
                    </>
                ),
            },
            {
                cell: (row) => (
                    <Button color="secondary" className="mb-0" onClick={()=> handlEdit(row)}>Edit</Button>
                ),
            }
        ],
    );



    const handlEdit = (row) => {  
       //window.location = `/property/edit-billing/${id}?billing_id=${row?.id}`
      history.push({pathname: `/property/edit-billing/${id}/${row?.id}`});
   } 
    
    
    const fetchData = async (page, size = perPage, ) => {  
        ApiServices.propertyBillingByProperty({'page' : page, 'per_page': size, id: parseInt(id) })
        .then((result) => {     
            setData(result?.data?.data)
            setRowCount(result.data.data.length)
            setTotalRows(result.data.total)
            setLoading(false)
            if(isTotal){
                isTotal(result.data.total)
            }
        })
        .catch(function (error) {
        });
    };

    useEffect(() => {
        if(id){
            saerch_ref.current = id;
            fetchData(1);
        }
    }, [id]);
    

   
    const handlePageChange = (page) => {
        fetchData(page);
        setCurrentPage(page);
    };

    const handlePerRowsChange = async (newPerPage, page) => { 
        fetchData(page, newPerPage);
        setPerPage(newPerPage);
    };


    const dataTable = []
    const list = data

    for (let index = 0; index < list.length; index++) {
        dataTable.push({
           id: list[index].id,
           year: list[index].year,
           noticedValue: `$${list[index].noticedValue.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}`,
           settledValue: `$${list[index].settledValue.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}`,
           reduction: `$${list[index].reduction.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}`,
           taxrate: `${list[index].taxrate}%`,
           taxsavings: `$${list[index].taxsavings.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}`,
           billingPercentage: `${list[index].billingPercentage}%`,
           invoiceamountOrig: list[index].invoiceamount,
           invoiceamount: `$${list[index].invoiceamount.toFixed(2).replace(/\d(?=(\d{3})+\.)/g, '$&,')}`,
           result: list[index].result_name,
           agent_id: list[index].agent_fname ? list[index].agent_fname + ' ' + list[index].agent_lname:'',
        })
        
    }
    
    return (
        <div className="activeProperties__wrapper pt-4">
            <Row>
                <Col md={6}>
                    <p className="tab-content-title">Property billing</p>
                </Col>
                <Col md={6} className="text-right">
                    <Link to={`/property/add-billing/${id}`} className="btn btn-primary mb-1">Add Billing</Link>
                </Col>
                <Col md={12}>
                    <DataTable
                    //title="Users"
                        columns={columns}
                        data={dataTable}
                        progressPending={loading}
                        pagination={false}
                        paginationServer
                        paginationTotalRows={totalRows}
                        paginationDefaultPage={currentPage}
                        striped={TABLE_SETTINGS.STRIPED}
                        highlightOnHover={true}
                        onChangeRowsPerPage={handlePerRowsChange}
                        onChangePage={handlePageChange}
                        paginationPerPage={TABLE_SETTINGS.PAGINATION_PER_PAGE}
                        paginationRowsPerPageOptions={TABLE_SETTINGS.ROW_PER_PAGE}
                        noDataComponent={<Empty />}
                    />
                    <CustomPagination rowCount={rowCount} currentPage={currentPage} totalRows={totalRows} perPage={perPage} handlePageChange={handlePageChange} />
                </Col>
            </Row>
       </div>
    );
};

export default PropertyBilling;
