import React, { useState, useEffect } from 'react';
import { Col, Row, Table, Button } from 'reactstrap';
import { Tab, Tabs } from 'react-bootstrap';
import { Link } from 'react-router-dom';
import DataTable from "react-data-table-component";
import ApiServices from "@/utils/ApiServices";
import DashboardActivities from '../../../Activities/components/DashboardActivities';
import AdminActivities from '../../../Activities/components/adminActivities';
import ClientActivities from '../../../Activities/components/clientActivities';

const RecentActivities = (props) => {
    const [countAll,setCountAll] = useState(0)
    const [countAll2,setCountAll2] = useState(0)
    const [countAll3,setCountAll3] = useState(0)

    useEffect(() => { 
        //fetchActivities();
    }, [props]);

    const fetchActivities = () => {
        ApiServices.allActivities().then((result) => {     
          setActivities(result.data)
        })
        .catch((error) => {
           // globalError()
        })
    }

    return (
        <Row>
            <Col md={12} className="mb-3">
                <div className="dashboard-card-title">
                    <p>Recent Activities</p>
                </div>
            </Col>
            <Col md={12}>
                <Tabs defaultActiveKey="all" className="recentactivities-wrapper">
                    <Tab eventKey="all"  className="py-4" title={<>All {countAll !== 0 && <span className={`tab-counter tab-length-${countAll?.toString().length}`}>{`${countAll}`}</span> }</>}>
                        <DashboardActivities  type="all"  isTotal={(value)=> setCountAll(value)}   />
                    </Tab>
                    <Tab eventKey="admins" className="py-4" title={<>Admin {countAll2 !== 0 && <span className={`tab-counter tab-length-${countAll2?.toString().length}`}>{`${countAll2}`}</span> }</>}>
                        <DashboardActivities  type="admin"  isTotal={(value)=> setCountAll2(value)} />
                    </Tab>
                    <Tab eventKey="clients" title="Clients" className="py-4" title={<>Client {countAll3 !== 0 && <span className={`tab-counter tab-length-${countAll3?.toString().length}`}>{`${countAll3}`}</span> }</>}>
                       <DashboardActivities  type="client" isTotal={(value)=> setCountAll3(value)} />
                    </Tab>
                </Tabs>
            </Col>
        </Row>
    );
}

export default React.memo(RecentActivities);