/* eslint-disable */
import React, { useEffect, useState } from 'react';
import queryString from 'query-string';
import { 
    Card, CardBody, Col, Container, Row, Spinner,
  } from 'reactstrap';
import {  reduxForm } from 'redux-form';
import { useForm } from "react-hook-form";
import { FormField, FormSelect } from "@/shared/components/FormField";
import { Link, useHistory, useParams, useLocation } from 'react-router-dom';
import { Button } from 'reactstrap';
import TitleBreadCrumbs from '@/components/TitleBreadCrumbs'
import { successMsg, globalError, formError } from "../../utils/message_herlper"
import ApiServices from "@/utils/ApiServices";

const AddBilling = (props) => {
    let search = queryString.parse(props.location.search)  
    let { id } = useParams()
    const history = useHistory()
    const location = useLocation();
    const currentYear = (new Date()).getFullYear()
    const range = (start, stop, step) => Array.from({ length: (stop - start) / step + 1}, (_, i) => start + (i * step))
    const [details, setDetails] = useState(null)
    const [pdetails, setpDetails] = useState(null)
    const [years, setYears] = useState(range(currentYear, currentYear - 4, -1))
    const [billingID,setBillingID] = useState()
    const [resultList, setResultList] = useState([])
    const [agentList, setAgentList] = useState([])
    const [isSubmit, setSubmit] = useState(false);
    const [noSubmit, setNoSubmit] = useState(0);  
    const { handleSubmit, reset, control, errors, register, setValue, clearErrors, watch, getValues , formState} = useForm()

    useEffect(() => {    
        setNoSubmit(formState?.submitCount || 0)
        if (formState.isSubmitted && Object.keys(formState?.errors).length > 0 && formState?.submitCount != noSubmit ) {
            formError()
            return
        }
    }, [formState])

    useEffect(() => {   
        let {id, billing_id} =  props?.match?.params
        propertyDetails()
        setBillingID(id)
        if(billing_id){  
            setYears(range(currentYear, currentYear - 11, -1))
            ApiServices.propertyBillingDetails(billing_id).then((result) => {  
                setpDetails(result.data)
                let billing = result.data; 
                setValue('year', billing?.year)
                setValue('agent', billing?.agent_id)
                setValue('result', billing?.pr_id)
                setValue('billingPercentage', billing?.billingPercentage)
                setValue('noticedValue', billing?.noticedValue)
                setValue('settledValue', billing?.settledValue)
                setValue('taxrate', billing?.taxrate)
                setValue('reduction', billing?.reduction)
                setValue('taxsavings', billing?.taxsavings)
                setValue('invoiceamount', billing?.invoiceamount)
            })
            .catch((error) => {
                formError();
            })
        }else{
            setValue('year', currentYear)
            
        }
       
    }, [location]);

    useEffect(() => { 
        resultsList()
        getagentList()
    }, [props]); 
    
    const  resultsList = () => {
        ApiServices.resultsList().then((result) => {   
           setResultList(result?.data)
        })
    }

    const  getagentList = () => {
        ApiServices.agentListActive().then((result) => { 
            setAgentList(result?.data)
        })
    }
    
    const propertyDetails = () => { 
        let {billing_id} =  props?.match?.params
        ApiServices.propertyDetails(id).then((result) => {  
            if(!billing_id){ 
                setValue('billingPercentage', result?.data?.accnt_percent_of_savings) 
            }
            setDetails(result?.data);
        })
        .catch((error) => {
            formError();
        })
    }

    const handleFormSubmit = (form) => {
        setSubmit(true);
    
        if(!pdetails){
            form.id = id
            ApiServices.propertyBilling(form).then((result) => {    
                setTimeout(() => {
                    successMsg('PROPERTY_BILLING_SUCCESS') 
                    history.push({pathname: `/property/details/${id}`});
                },1000)
            })
            .catch((error) => { 
                setSubmit(false);
                globalError()
            })
        }else{
            form.id = pdetails?.id
            ApiServices.updatePropertyBilling(form).then((result) => {    
                setTimeout(() => {
                    successMsg('PROPERTY_BILLING_EDIT_SUCCESS') 
                    history.push({pathname: `/property/details/${id}`});
                },1000)
            })
            .catch((error) => {
                setSubmit(false);
                globalError()
            })
        }
        
    }
    const onchangeYear = (value) => {
        setValue('year', value)
        clearErrors('year')
    }

    const onchangeResult = (value) => {
        setValue('result', value)
        clearErrors('result')
    }

    const onchangeAgent = (value) => {  
        setValue('agent', value)
        clearErrors('agent')
    }

    const formChange = (form) => {
        var nValue = parseFloat(form?.noticedValue || 0 )
        var sValue = parseFloat(form?.settledValue || 0 )
        var tRate = parseFloat(form?.taxrate || 0 )
        var bRate =   parseFloat(form?.billingPercentage || 0 )
        bRate = bRate / 100;
        var aReduction = nValue - sValue;
        var tSaving = aReduction * tRate;
        var iAmount = tSaving * bRate;
        setValue('reduction', aReduction?.toFixed(2))
        setValue('taxsavings', tSaving?.toFixed(2))
        setValue('invoiceamount', iAmount?.toFixed(2))
    }
    
   

    const onCancel = () => { 
        history.push({pathname: `/property/details/${id}`});
    }

    return (
        <Container>
            <TitleBreadCrumbs title="Property Billing" >
                <div className="page-breadcrumbs">
                <Link to="/dashboard">Dashboard</Link>
                    <span className="current_breadcrumbs">Billing</span>
                </div>
            </TitleBreadCrumbs>
            <Row>
                <Col md={12}>
                    <Card className="card-controlled">
                        <CardBody className="cardbody-controlled client-registration-form-container">
                            <Row>
                                <Col md={12}>
                                    <form className="client-reg-form"  onSubmit={handleSubmit(handleFormSubmit)} onChange={(form) => formChange(getValues())}>
                                        <h4> {billingID ? 'Edit' : 'Create' } Billing Information for Property: {details?.property_address} {details?.property_city}, {details?.property_state} {details?.property_zip}</h4>
                                        <hr />
                                        <div className="form__form-group-two">
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Current Tax Year:</span>
                                                <div className="form__form-group-field">
                                                    <FormSelect
                                                        name="year"
                                                        placeholder="Select  Year"
                                                        rules={{
                                                            required: "This is required field",
                                                        }}
                                                        control={control}
                                                        options={years}
                                                        defaultValue={pdetails?.year ||  currentYear}
                                                        {...register("year", { required: "This is required field" })} 
                                                        onChange={(value)=> onchangeYear(value)}
                                                    />
                                                </div>
                                                {errors?.year && <span className="form__form-group-error">{errors?.year?.message}</span>}
                                            </div>
                                            <div>
                                                <span className="form__form-group-label">Billing Percentage:</span>
                                                <div className="form__form-group-field">
                                                     <FormField
                                                        name="billingPercentage"
                                                        component="input"
                                                        type="text"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "This is required field",
                                                            min: {
                                                                value: 0,
                                                                message: "Please enter valid value."
                                                            },
                                                            max: {
                                                                value: 100,
                                                                message: "Please enter valid value."
                                                            }
                                                        }}
                                                        placeholder=""
                                                    />
                                                </div>
                                                {errors?.billingPercentage && <span className="form__form-group-error">{errors?.billingPercentage?.message}</span>}
                                            </div>
                                        </div>
                                        <hr />
                                        <div className="form__form-group-three">
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Noticed Value:</span>
                                                <div className="form__form-group-field">
                                                    <FormField
                                                        name="noticedValue"
                                                        component="input"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "This is required field",
                                                            min: {
                                                                value: 0,
                                                                message: "Please enter valid value."
                                                            },
                                                        }}
                                                        placeholder=""
                                                    />
                                                </div>
                                                {errors?.noticedValue && <span className="form__form-group-error">{errors?.noticedValue?.message}</span>}
                                            </div>
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Settled Value:</span>
                                                <div className="form__form-group-field">
                                                    <FormField
                                                        name="settledValue"
                                                        component="input"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "This is required field",
                                                            min: {
                                                                value: 0,
                                                                message: "Please enter valid value."
                                                            },
                                                        }}
                                                        placeholder=""
                                                    />
                                                </div>
                                                {errors?.settledValue && <span className="form__form-group-error">{errors?.settledValue?.message}</span>}
                                            </div>
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Tax Rate:</span>
                                                <div className="form__form-group-field">
                                                    <FormField
                                                        name="taxrate"
                                                        component="input"
                                                        control={control}
                                                        errors={errors}
                                                        rules={{
                                                            required: "This is required field",
                                                            min: {
                                                                value: 0,
                                                                message: "Please enter valid value."
                                                            },
                                                        }}
                                                        placeholder=""
                                                    />
                                                </div>
                                                {errors?.taxrate && <span className="form__form-group-error">{errors?.taxrate?.message}</span>}
                                            </div>
                                        </div>
                                        <div className="form__form-group-two">
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Result:</span>
                                                <div className="form__form-group-field">
                                                    <FormSelect
                                                        name="result"
                                                        placeholder="Select Result"
                                                        rules={{
                                                            required: "This is required field",
                                                        }}
                                                        control={control}
                                                        options={resultList}
                                                        isOject={true}
                                                        objectName="name"
                                                        defaultValue={parseInt(pdetails?.pr_id) || "DEFAULT"}
                                                        {...register("result")} 
                                                        onChange={(value)=> onchangeResult(value)}
                                                    />
                                                </div>
                                                {errors?.result && <span className="form__form-group-error">{errors?.result?.message}</span>}
                                            </div>
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Resulting Agent:</span>
                                                <div className="form__form-group-field">
                                                    <FormSelect
                                                        name="agent"
                                                        placeholder="Select Agent"
                                                        rules={{
                                                            required: "This is required field",
                                                        }}
                                                        control={control}
                                                        options={agentList}
                                                        isOject={true}
                                                        objectName="name"
                                                        defaultValue={parseInt(pdetails?.agent_id) || "DEFAULT"}
                                                        {...register("agent")} 
                                                        onChange={(value)=> onchangeAgent(value)}
                                                    />
                                                </div>
                                                {errors?.agent && <span className="form__form-group-error">{errors?.agent?.message}</span>}
                                            </div>
                                        </div>
                                        <hr />
                                        <div className="form__form-group-three">
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Amount of Reduction:</span>
                                                <div className="form__form-group-field">
                                                    <FormField
                                                        name="reduction"
                                                        component="input"
                                                        type="number"
                                                        step="0.01"
                                                        control={control}
                                                        errors={errors}
                                                        placeholder=""
                                                        disabled
                                                    />
                                                </div>
                                                {errors?.reduction && <span className="form__form-group-error">{errors?.reduction?.message}</span>}
                                            </div>
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Tax Savings:</span>
                                                <div className="form__form-group-field">
                                                    <FormField
                                                        name="taxsavings"
                                                        component="input"
                                                        type="number"
                                                        step="0.01"
                                                        control={control}
                                                        errors={errors}
                                                        placeholder=""
                                                        disabled
                                                    />
                                                </div>
                                                {errors?.taxsavings && <span className="form__form-group-error">{errors?.taxsavings?.message}</span>}
                                            </div>
                                            <div className="form__form-group">
                                                <span className="form__form-group-label">Invoice Amount:</span>
                                                <div className="form__form-group-field">
                                                    <FormField
                                                        name="invoiceamount"
                                                        component="input"
                                                        type="number"
                                                        step="0.01"
                                                        control={control}
                                                        errors={errors}
                                                        placeholder=""
                                                        disabled
                                                    />
                                                </div>
                                                {errors?.invoiceamount && <span className="form__form-group-error">{errors?.invoiceamount?.message}</span>}
                                            </div>
                                        </div>
                                        <div className="client-create-btns float-right">
                                            <Button color="secondary" className="cancel__btn" type="button" onClick={()=> onCancel()}>
                                                Cancel
                                            </Button>
                                            <Button type="submit" color="primary"  disabled={isSubmit}>
                                            {isSubmit ? <Spinner size="sm" color="secondary" className="mr-2" /> : ''} Submit
                                            </Button>
                                        </div>
                                    </form>
                                </Col>
                            </Row>
                        </CardBody>
                    </Card>
                </Col>
            </Row>
        </Container>
    )
}

export default reduxForm({
    form: 'billing_create',
})(AddBilling);